.{
  margin: 0;
  padding:0;
  box-sizing: border-box;
}

body{
  font-family: 'montseratt',sans-serif;
}

.app{
  background-image: url("./1.jpg");
  background-size: cover;
  background-position: bottom;
  transition: 0.8 ease;
}
.appwarm{
  background-image: url("./2.jpg");
}
main{
  height: 100vh;
  background-image: linear-gradient(to bottom,rgba(0,0,0,0.1),rgba(0,0,0,0.35));
  padding: 25px;
}
.search-box{
  width:100px;
  margin: 0 0 75px;
}
.search-bar{
  display: block;
  width: 400px;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color:rgba(255,255,300,0.5);
  border-radius:0px 0px 16px 16px ;
  margin-top: -25px;
  box-shadow: 0px 5px rgba(0,0,0,0.3);
  color: #313131;
  font-size: 20px;
  transition: 0.4 ease;

}
.search-bar:focus{
  background-color: rgba(255,255,300,0.8);
}
.location-box .location{
color: #fff;
font-size: 32px;
font-weight: 500;
text-align: center;
text-shadow: 3px 3px rgba(50,50,70,0.5);

}

.location-box .date{
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  text-align: center;
  text-shadow: 2px 2px rgba(50,50,70,0.5);

}
.weather-box{
  text-align: center;
}
.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto ;
  background-color: rgba(255,255,255,0.3);
  border-radius: 16px;
  padding: 15px 25px;
  color: #fff;
  font-size: 102px;
  font-weight: 600;
  text-shadow:3px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);


}
.weather-box .weather{
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow:3px 3px rgba(50,50,70,0.5);
}